<template>
    <div>
        <v-form @submit.prevent="addTodo">
            <v-text-field type="text" v-model="title"></v-text-field>
            <v-btn type="submit">Add</v-btn>
        </v-form>
    </div>
</template>

<script>
export default {
    name: 'AddTodo',
    data() {
        return {
            title: ''
        }
    },
    methods: {
        addTodo() {
            let newTodo = {
                id: 4,
                title: this.title,
                completed: false
            }
            this.$emit('add-todo', newTodo)
        }
    }
}
</script>

<style scoped>
    .todo-item {
        background: lightcoral;
    }
    .is-completed {
        background: lightgreen;
    }
</style>